
import { computed, defineComponent, reactive, SetupContext } from "vue";

type Porps = {
  text: string;
  checked: boolean;
};

export default defineComponent({
  name: "Input-Checkbox",
  props: {
    text: {
      type: String,
      required: true,
    },
    checked: {
      type: Boolean,
      required: true,
    },
  },
  setup(props: Porps, context: SetupContext) {
    const value = computed({
      get: () => props.checked,
      set: (value: boolean) => context.emit("update:checked", value),
    });
    const state = reactive({
      id:
        new Date().getTime().toString(16) +
        Math.floor(Math.random() * new Date().getTime()).toString(16) +
        new Date().getTime().toString(16),
    });

    return {
      value,
      state,
    };
  },
});
