
import { computed, defineComponent, SetupContext } from "vue";

type Porps = {
  text: string;
  color: string;
};

export default defineComponent({
  name: "Input-Color",
  props: {
    text: {
      type: String,
      required: true,
    },
    color: {
      type: String,
      required: true,
    },
  },
  setup(props: Porps, context: SetupContext) {
    const value = computed({
      get: () => props.color,
      set: (value: string) => context.emit("update:color", value),
    });

    return {
      value,
    };
  },
});
