
import { computed, defineComponent, SetupContext } from "vue";

type Porps = {
  text: string;
  pixel: number;
};

export default defineComponent({
  name: "Input-Pixel",
  props: {
    text: {
      type: String,
      required: true,
    },
    pixel: {
      type: Number,
      required: true,
    },
  },
  setup(props: Porps, context: SetupContext) {
    const value = computed({
      get: () => props.pixel,
      set: (value) => {
        if (!value) value = 0;
        context.emit("update:pixel", value);
      },
    });

    return {
      value,
    };
  },
});
