
import { computed, defineComponent, SetupContext } from "vue";

type Porps = {
  font: string;
};

export default defineComponent({
  name: "Input-Font",
  props: {
    font: {
      type: String,
      required: true,
    },
  },
  setup(props: Porps, context: SetupContext) {
    const value = computed({
      get: () => props.font,
      set: (value) => {
        context.emit("update:font", value);
      },
    });

    return {
      value,
    };
  },
});
