
import { computed, defineComponent, SetupContext } from "vue";

type Porps = {
  text: string;
  date: string;
};

export default defineComponent({
  name: "Input-Date",
  props: {
    text: {
      type: String,
      required: true,
    },
    date: {
      type: String,
      required: true,
    },
  },
  setup(props: Porps, context: SetupContext) {
    const value = computed({
      get: () => props.date,
      set: (value) => context.emit("update:date", new Date(value)),
    });

    return {
      value,
    };
  },
});
