
import { computed, defineComponent, SetupContext } from "vue";

type Porps = {
  label: string;
  text: string;
};

export default defineComponent({
  name: "Input-Font",
  props: {
    label: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
  },
  setup(props: Porps, context: SetupContext) {
    const value = computed({
      get: () => props.text,
      set: (value) => {
        context.emit("update:text", value);
      },
    });

    return {
      value,
    };
  },
});
